import React, { useState, useEffect, useRef } from 'react';
import './Navbar.css';
import { BrowserRouter, Routes, Route, Link } from 'react-router-dom';
import Home from '../home_p/Home';
import Products from '../Products/Products';
import Topdeal from '../TopDeal/Topdeal';
import About from '../About_Us/About';
import Contact_Us from '../Contact_Us/Contact_Us';
import ProductDetail from '../ProductDetail/ProductDetail';
import logo from '../images/favicon.ico.png'

const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const menuRef = useRef();
  const hamburgerRef = useRef();

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const closeMenu = () => {
    setIsMenuOpen(false);
  };

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (menuRef.current && !menuRef.current.contains(event.target) &&
          hamburgerRef.current && !hamburgerRef.current.contains(event.target)) {
        closeMenu();
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);
 

  return (
    <div className='Navbar'>
      <div className='logo'>
        <Link to='/'><img src={logo} alt="" /></Link> 
      </div>
      <div ref={menuRef} className={`menu ${isMenuOpen ? 'active' : ''}`}>
        <li><Link to='/' onClick={closeMenu}>Home</Link></li>
        <li><Link to='/topdeal' onClick={closeMenu}>Top Deal</Link></li>
        <li><Link to='/product' onClick={closeMenu}>Products</Link></li>
        <li><Link to='/about-us' onClick={closeMenu}>About Us</Link></li>
        <li><Link to='/order' onClick={closeMenu}>Order</Link></li>
      </div>
      <div ref={hamburgerRef} className='hamburger' onClick={toggleMenu}>
        {/* Hamburger Icon */}
        <span className='bar'></span>
        <span className='bar'></span>
        <span className='bar'></span>
      </div>
    </div>
  );
};

const App = () => {
  const [cart, setCart] = useState([]);  
  
  const addToCart = (product) => {  
   setCart((prevCart) => [...prevCart, product]);  
  }; 

  

  return (
    <BrowserRouter>
      <Navbar />
      <Routes>
        <Route path='/' element={<Home />} />
        <Route path='/product' element={<Products addToCart={addToCart} />} />
        <Route path='/topdeal' element={<Topdeal />} />
        <Route path='/about-us' element={<About />} />
        <Route path='/order' element={<Contact_Us cart={cart}/>} />
        <Route path="/product/:id" element={<ProductDetail addToCart={addToCart}/>} />

      </Routes>
    </BrowserRouter>
  );
};

export default App;

