import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./Products.css";
import products from "../products";
import Contact from '../Contact/Contact'

const Products = ({ addToCart }) => {
  const [showPopup, setShowPopup] = useState(false);
  const navigate = useNavigate();

  const handleAddToCart = (product) => {
    addToCart(product);
    setShowPopup(true);
    setTimeout(() => setShowPopup(false), 2000);
  };

  const handleViewProduct = (id) => {
    navigate(`/product/${id}`); // Navigate to ProductDetail with product ID
  };

  return (
    <div className="Products">
      <h1 className="products_h1">RO Water Purifier</h1>

      {showPopup && <div className="popup_message">Item added to cart!</div>}

      <div className="Products_container">
        {products.map((product, index) => (
          <div key={index} className="product-card">
            <div className="Products_card">
              <img
                src={product.img}
                alt={product.title}
                className="Products_card_img"
              />
              <div className="Products_card_1">
                <h1 className="Products_card_1_h1">{product.title}</h1>
                <p className="Products_card_1_p">{product.price}</p>
                <button
                  onClick={() => handleAddToCart(product)}
                  className="Products_card_1_btn"
                >
                  Add to Cart
                </button>
                <button
                  onClick={() => handleViewProduct(product.id)}
                  className="Products_card_1_btn"
                >
                  View
                </button>
              </div>
            </div>
          </div>
        ))}
      </div>
      <Contact />
    </div>
  );
};

export default Products;
