import React, { useState } from 'react';
import './ProductDetail.css';
import { useNavigate,useParams } from "react-router-dom";
import products from "../products";
import Contact from '../Contact/Contact'

const ProductDetail = ({ addToCart }) => {
    const [showPopup, setShowPopup] = useState(false);
  const navigate = useNavigate();

  const { id } = useParams();
  const handleAddToCart = (product) => {
    addToCart(product);
    setShowPopup(true);
    setTimeout(() => setShowPopup(false), 2000);
  };

  const handleViewProduct = (id) => {
    navigate(`/product/${id}`); // Navigate to ProductDetail with product ID
  };
  const product = products.find((p) => p.id === parseInt(id));

  if (!product) return <h2>Product not found</h2>;

  return (
    <>
    
    <div className="ProductDetail">
      <h1>{product.title}</h1>
      <div className='ProductDetail_card'>
      {showPopup && <div className="popup_message">Item added to cart!</div>}
        <div className='ProductDetail_card_1'>
            <img src={product.img} alt={product.title} /> 
            <img src={product.img} alt={product.title} /> 
            <img src={product.img} alt={product.title} /> 
            <img src={product.img} alt={product.title} /> 
        </div>
        
        <div className='ProductDetail_card_2'>
            <img src={product.img_1} alt={product.title} />
            <h2>Price: {product.price}</h2>
            <p>{product.description}</p>
            <div className='ProductDetail_card_2_1'>
                <h3>Capacity : {product.liter}</h3>
                <button
                    onClick={() => handleAddToCart(product)}
                >
                    Add to Cart
                </button>
            </div>
            
        </div>
      </div>
      
      
    </div>
    </>
  );
};

export default ProductDetail;
