import React from "react";

const Card = (props) => {
  return (
    <div className="Card">
      <div className="deal_card">
        <img src={props.img_1} alt="" className="deal_card_img" />
        <div className="deal_card_1">
            <h1>{props.title_1}</h1>
            <p>{props.para_1}</p>
        </div>
        
      </div>
      <div className="deal_card">
        <img src={props.img_2} alt="" className="deal_card_img" />
        <div className="deal_card_1">
            <h1>{props.title_2}</h1>
            <p>{props.para_2}</p>
        </div>
      </div>
      <div className="deal_card">
        <img src={props.img_3} alt="" className="deal_card_img" />
        <div className="deal_card_1">
            <h1>{props.title_3}</h1>
            <p>{props.para_3}</p>
        </div>
      </div>
    </div>
  );
};

export default Card;
