import React, { useRef } from "react";
import "./Contact_Us.css";
import Contact from "../Contact/Contact";
import emailjs from "@emailjs/browser";

const Contact_Us = ({ cart = [] }) => {
  const form = useRef();


  const sendEmail = (e) => {
    e.preventDefault();

    // Generate cart items as a string for the email message
    const cartDetails = cart
      .map((item) => `${item.title} - ${item.price}`)
      .join(", ");

    // Set up email parameters
    const emailParams = {
      user_name: form.current.user_name.value,
      user_phone: form.current.user_phone.value,
      user_email: form.current.user_email.value,
      message: form.current.message.value,
      products: cartDetails, // Add cart details to email parameters
    };

    emailjs
      .send(
        "service_1glhvpk",
        "template_16upf2l",
        emailParams,
        "GQjvV8V2Ws0HKjkD2"
      )
      .then(
        () => {
          console.log("SUCCESS!");
          alert("SUCCESS!");
          form.current.reset(); // Clears the form inputs after success
          
        },
        (error) => {
          console.log("FAILED...", error.text);
        }
      );
  };

  return (
    <>
      <div className="contact_us_section">
        <h1 className="contact_us_h1">Order Now </h1>
        <div className="contact_us_card">
          <img
            src="image/Contact.png"
            alt="Image is not found"
            className="contact_us_card_img"
          />
          <form ref={form} onSubmit={sendEmail}>
            <div className="contact_us_card_list">
              <label>Name</label>
              <input placeholder="Name" name="user_name" />
              <label>Phone No</label>
              <input placeholder="Your Mobile number" name="user_phone" />
              <label>Mail</label>
              <input placeholder="Your Mail id" name="user_email" />
              <label>Address</label>
              <textarea placeholder="Your Address" name="message" />
              <button type="submit">Order Now</button>
            </div>
          </form>
        </div>
      </div>
      <div className="cart-details">
        <h3 className="details_h3">Your Cart List</h3>
        {cart.length === 0 ? (
          <p className="details_p">Your cart is empty.....</p>
        ) : (
          <ul className="details_ul">
            {cart.map((item, index) => (
              <li key={index} className="details_li">
                <img
                  src={item.img}
                  alt={item.title}
                  width="50"
                  className="details_img"
                />
                <div className="details_list">
                  <p>{item.title}</p>
                  <p>{item.price}</p>
                </div>
              </li>
            ))}
          </ul>
        )}
      </div>
      <Contact />
    </>
  );
};

export default Contact_Us;
